import React, {Component} from 'react';

class Cake extends React.Component {

    render() {
        const defaultImageUrl= "https://www.eglsf.info/wp-content/uploads/image-missing.png";
        return (<div className="bg-green-200 rounded-lg container p-3 center m-1 md:w-auto flex flex-col flex-wrap content-center ">
                    <img
                        height="250"
                        width="250"
                        src={this.props.image}
                        onError={(e)=>{e.target.onerror = null; e.target.src=defaultImageUrl } } />
                    <div className="text-center font-semibold">{this.props.title}</div>
                    <div className="text-center text-sm" >{this.props.description}</div>
                </div>)
      }
}

export default Cake;
